:root {
  --Main-Background: linear-gradient(196deg, #341010 -10.52%, #0C1826 -1.38%, #000 26.76%);
  --Black-Color: #000;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  background: var(--Main-Background) var(--Black-Color);
  background-size: cover;
  background-repeat: no-repeat;

}
@font-face {
  font-family: 'dymaxon';
  src: url('../fonts/Dymaxion script–°.ttf') format('truetype');
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body::-webkit-scrollbar {
  width: 11px;               /* —à–∏—Ä–∏–Ω–∞ –≤—Å–µ–π –ø–æ–ª–æ—Å—ã –ø—Ä–æ–∫—Ä—É—Ç–∫–∏ */
}

body::-webkit-scrollbar-track {
  background: #272727;        /* —Ü–≤–µ—Ç –∑–æ–Ω—ã –æ—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏—è */
}

body::-webkit-scrollbar-thumb {
  background-color: rgba(21, 20, 20, 0.93);    /* —Ü–≤–µ—Ç –±–µ–≥—É–Ω–∫–∞ */
  border-radius: 20px;       /* –æ–∫—Ä—É–≥–ª–æ—Å—å –±–µ–≥—É–Ω–∫–∞ */
  border: 3px solid #272727;  /* –æ—Ç—Å—Ç—É–ø –≤–æ–∫—Ä—É–≥ –±–µ–≥—É–Ω–∫–∞ */
}
@media (prefers-reduced-motion: no-preference) {
  :root{
    scroll-behavior: smooth;
  }
}
